.button {
  display: inline-block;

  padding: 7px 25px 8px 25px;

  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: #fff;
  text-transform: uppercase;

  background: #FF0808;
  border: 0;
  border-radius: 25px;

  transition: 0.2s;
}
.button:hover {
  background: #CC0000;
}
